.App {
  text-align: center;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  /* grid-template-rows: repeat(5, 1fr); */
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  margin-left:50px;
  margin-right:50px;
  position: sticky;
  top: 0;
}

html { 
  background:  rgb(181, 181, 181) no-repeat center center fixed; 
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.div1 { 
  grid-area: 1 / 1 / 2 / 6; 
  background-image: url('./assets/food-image.png'); 
  min-height:auto;  
  background-color: rgba(0, 0, 0, 0.5); 
  background-blend-mode: multiply;}
.div2 { grid-area: 2 / 1 / 6 / 2;
  height: 790px;
  width: 500px;
  overflow-y: scroll;
}
.div3 { grid-area: 2 / 2 / 6 / 6; }

#title{
  color:white;
}

#sortByLabel{
  color:white;
}

.businessInfoName{
  padding-top:90px;
}

div .businessParentDiv{
  margin-top:130px;
}